<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">分配</span>
				</div>
			</div>
			<div class="cash_flow_table table">
				<el-table :data="datalistTotal" border style="width: 100%" empty-text="" max-height='350'>
					<el-table-column prop="plan_receive_date" label="计划到账日期" width="180">
						<template slot-scope="scope">
							<el-date-picker v-model="scope.row.plan_receive_date" @change="chooseDate" :disabled="scope.$index != currentIndex&&!is_add_page" type="date" placeholder="选择日期" value-format="yyyy-MM-dd" :editable='false'>
							</el-date-picker>
						</template>
					</el-table-column>
					<el-table-column prop="event_name" label="事件" width="280">
						<template slot-scope="scope">
							<el-input v-model="scope.row.event_name" placeholder="项目名称-第N次分配"  :disabled="scope.$index != currentIndex&&!is_add_page" clearable>
							</el-input>
						</template>
					</el-table-column>
					
					<el-table-column prop="quit_date" label="退出日期" width="180">
						<template slot-scope="scope">
							<el-date-picker v-model="scope.row.quit_date" :editable='false' :disabled="scope.$index != currentIndex&&!is_add_page" type="date" placeholder="选择日期"  value-format="yyyy-MM-dd">
							</el-date-picker>
						</template>
					</el-table-column>
					<el-table-column prop="quit_method" label="退出方式" width="180">
						<template slot-scope="scope">
							<el-select v-model="scope.row.quit_method" placeholder="请选择" clearable  :disabled="scope.$index != currentIndex&&!is_add_page">
								<el-option v-for="item in typeList" :key="item.value" :label="item.label" :value="item.value">
								</el-option>
							</el-select>
						</template>
					</el-table-column>
					<el-table-column prop="distribute_capital" label="分配本金(万元)" width="150">
					</el-table-column>
					<el-table-column prop="distribute_profit" label="分配收益(万元)(税前)" width="150">
					</el-table-column>
					<el-table-column prop="distribute_amount" label="分配总额(万元)" width="150">
					</el-table-column>
					<el-table-column prop="distribute_rate" label="本金占比" width="140" v-if="!is_add_page">
						<template slot-scope="scope">
							<div class="flex_between">
								<el-input v-model="scope.row.distribute_rate" placeholder="请填写"  :disabled="scope.$index != currentIndex&&!is_add_page" clearable>
								</el-input>
								&nbsp;&nbsp;%
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="distribute_method" label="分配方式" width="100">
						<template slot-scope="scope">{{scope.row.distribute_method}}</template>
					</el-table-column>
					<!-- <el-table-column prop="quit_times" label="项目本次退出倍数" width="150">
						<template slot-scope="scope">{{scope.row.quit_times}}</template>
					</el-table-column> -->
					<el-table-column prop="unit" label="币种" width="100">
						<template slot-scope="scope">{{scope.row.unit}}</template>
					</el-table-column>
					<el-table-column prop="remark" label="备注" width="180">
						<template slot-scope="scope">
							<el-input v-model="scope.row.remark" placeholder="请输入内容"  :disabled="scope.$index != currentIndex&&!is_add_page" clearable>
							</el-input>
						</template>
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="150" v-if='!is_add_page'>
						<template slot-scope="scope">
							<el-button v-if="scope.$index != currentIndex" @click="editTotal(scope.$index,scope.row)" type="text" size="small">编辑</el-button>
							<el-button v-if="scope.$index == currentIndex" @click="editSaveTotal(scope.$index,scope.row)" type="text" size="small">保存</el-button>
							<el-button v-if="scope.$index == currentIndex" @click="editCancelTotal(scope.$index,scope.row)" type="text" size="small">取消</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
			</div>
		</div>
		
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">明细</span>
				</div>
			</div>
			<div class="cash_flow_table">
				<div class="table">
					<el-table :data="datalistDetail" border style="width: 100%" empty-text="" sortable max-height='350'>
						<el-table-column prop="index" label="序号" width="100" sortable>
						</el-table-column>
						<el-table-column prop="name" label="认购基金名称" width="250" sortable>
						</el-table-column>
						<el-table-column prop="paied" label="实缴出资(万元)" width="150" sortable>
						</el-table-column>
						<el-table-column prop="paied_rate" label="实缴占比" width="150" sortable>
							<template slot-scope="scope">
								{{ scope.row.paied_rate }}&nbsp;%
							</template>
						</el-table-column>
						<el-table-column prop="detail_quit_date" label="到账日期" width="180" sortable>
							<template slot-scope="scope">
								<el-date-picker v-model="scope.row.detail_quit_date"  type="date" placeholder="选择日期"  value-format="yyyy-MM-dd"  :disabled="scope.$index != currentIndexDetail&&!is_add_page" clearable :editable='false'>
								</el-date-picker>
							</template>
						</el-table-column>
						<el-table-column prop="capital_distribute" label="分配本金(万元)" width="180" sortable>
							<template slot-scope="scope">
								<el-input v-model.number="scope.row.capital_distribute" v-on:input="calculate(scope.$index)"  placeholder="请输入内容"  :disabled="scope.$index != currentIndexDetail&&!is_add_page" clearable>
								</el-input>
							</template>
						</el-table-column>
						<el-table-column prop="profit_distribute_b" label="分配收益(万元)(税前)" width="180" sortable>
							<template slot-scope="scope">
								<el-input v-model.number="scope.row.profit_distribute_b" v-on:input="calculate()" placeholder="请输入内容"  :disabled="scope.$index != currentIndexDetail&&!is_add_page" clearable>
								</el-input>
							</template>
						</el-table-column>
						<el-table-column prop="profit_distribute_a" label="分配收益(万元)(税后)" width="180" sortable>
							<template slot-scope="scope">
								<el-input v-model.number="scope.row.profit_distribute_a" v-on:input="calculate()" placeholder="请输入内容"  :disabled="scope.$index != currentIndexDetail&&!is_add_page" clearable>
								</el-input>
							</template>
						</el-table-column>
						<el-table-column prop="distribute_amount" label="分配总额(万元)" width="150" sortable>
						</el-table-column>
						<el-table-column prop="all_distribute_amount" label="累计分配金额(万元)" width="180" sortable>
						</el-table-column>
						<el-table-column prop="all_distribute_rate" label="累计分配率" width="180" sortable>
							<template slot-scope="scope">
								{{ scope.row.all_distribute_rate }}&nbsp;%
							</template>
						</el-table-column>
						<el-table-column fixed="right" label="操作" width="120" v-if="!is_add_page">
							<template slot-scope="scope">
								<div class="" >
									<el-button v-if="scope.$index != currentIndexDetail" @click="editDetail(scope.$index,scope.row)" type="text" size="small">编辑</el-button>
									<el-button v-if="scope.$index == currentIndexDetail" @click="editSaveDetail(scope.$index,scope.row)" type="text" size="small">保存</el-button>
									<el-button v-if="scope.$index == currentIndexDetail" @click="editCancelDetail(scope.$index,scope.row)" type="text" size="small">取消</el-button>
								</div>
								<!-- <div class="" v-else>
									<el-button v-if='is_add_page' @click="deleteDetailItem(scope.$index,scope.row)" type="text" size="small">删除</el-button>
								</div> -->
							</template>
						</el-table-column>
						<template slot="empty">
							<div class="table-nodata">
								<img src="../../images/table_nodata.png"> 没有对应数据
							</div>
						</template>
					</el-table>
				</div>
				<div  class="submit_btn_box flex_center">
					<button @click="addCancel" class="hover_btn">取消</button>
					<button @click="saveAddInfo" class="blue_btn">保存</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { getCompanyRunCashIncomeManageAddInfoApi,addCompanyRunCashIncomeManageApi,getCompanyRunCashIncomeManageApi,editSaveTotalCompanyRunCashIncomeManageApi,editSaveDetailCompanyRunCashIncomeManageApi,getEventByDateApi } from '../../config/api'
	import {getFpDetailInfo,saveDistribute,getDistributeDetail} from '../../config/apiSj'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	export default {
		inject: ['reload'],
		
		data() {
			return {
				id: '',
				is_add_page: '',//是否是添加页面
				count: '',//查看页面传过来的参数
				isEditDetailShow: true,
				// 现金流总数据和详情数据开始
				datalistDetail: [],
				datalistTotal: [
					{	
						event_name:'',
						plan_receive_date:'',
						quit_date:'',
						quit_method:null,
						distribute_capital:0,
						distribute_profit:0,
						// distribute_amount:0,
						distribute_rate:0,
						distribute_method:'现金分配',
						quit_times:0,
						unit:decodeURIComponent(this.$route.query.unit),
						remark:'',
						event_count: '',
					},
				],
				// quit_times:'',//项目本次退出倍数
				currentIndex: -1,
				currentIndexDetail: -1,


				//退出方式列表
				typeList:[
					{
						value:1,
						label:'境内上市'
					},
					{
						value:2,
						label:'境外上市'
					},
					{
						value:3,
						label:'新三板挂牌'
					},
					{
						value:4,
						label:'协议转让'
					},
					{
						value:5,
						label:'整体收购'
					},
					{
						value:6,
						label:'企业回购'
					},
					{
						value:7,
						label:'清算'
					},
					{
						value:8,
						label:'被投企业分红'
					},
				],

				editorTotalData:null,
				editorDetailData:null,
				
				fp_time:'',
			}
		},
		created() {
			this.id = this.$route.query.id;
			if (this.$route.query.add) {
				this.is_add_page = this.$route.query.add;
				
				this.getAddInfo()
			} else {
				this.count = this.$route.query.count;
				this.getInitData()
			}
		},
		mounted() {
			this.calculate()
		},
		methods: {
			//---------------- 添加功能模块开始
			getAddInfo(){
				getFpDetailInfo({
					direct_invest_id: this.id
				}).then(res => {
					if(res.data.msgNo == 1) {
						// console.log(res);
						let data = res.data.payload.data;
						this.fp_time = res.data.payload.total+1;
//						this.datalistTotal[0].event_name = decodeURIComponent(this.$route.query.name) + '-第'+this.fp_time+'次分配'
						data.forEach((element,index) => {
							element.accumulation_distribute = parseFloat(element.accumulation_distribute);
							element.index = index+1;
							element.capital_distribute = 0;
							element.profit_distribute_b = 0;
							element.profit_distribute_a = 0;
							element.distribute_amount = 0;
							element.all_distribute_amount = element.accumulation_distribute;
							element.all_distribute_rate = 0;
						});
						this.datalistDetail = data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//删除明细
			deleteDetailItem(index,e){
				this.datalistDetail.splice(index,1);
				this.calculate()
			},
			//保存按钮
			saveAddInfo(){
				let commitDetailDataObj = JSON.parse(JSON.stringify(this.datalistTotal[0]));
				if(!this.is_add_page){
					commitDetailDataObj.distribute_rate = commitDetailDataObj.distribute_rate;
				}
				commitDetailDataObj.direct_invest_id = this.id;

				let newArr = [];
				let datalistDetail = JSON.parse(JSON.stringify(this.datalistDetail));
				// console.log(datalistDetail);
				// return false;
				datalistDetail.forEach((element,index) => {
					let newObj = {
						fund_name: element.name,
						real_amount:element.paied,
						real_rate:element.paied_rate/100,
						capital_distribute:element.capital_distribute,
						profit_distribute_b:element.profit_distribute_b,
						profit_distribute_a:element.profit_distribute_a,
						distribute_amount:element.distribute_amount,
						all_distribute_amount:element.all_distribute_amount,
						all_distribute_rate:element.all_distribute_rate,
						detail_quit_date:element.detail_quit_date,
					}
					if(this.is_add_page){
						newObj.p_id = element.p_id;
						newObj.sub_fund_type = parseInt(element.sub_fund_type);
					}else{
						newObj.id = element.id;
						newObj.sub_fund_type = parseInt(element.sub_fund_type);
					}
					newArr.push(newObj);
				});
				// console.log(newArr);
				// return false;
				commitDetailDataObj.distribute_detail_infos = JSON.stringify(newArr);
				saveDistribute(commitDetailDataObj).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", '保存成功');
						this.$router.push({
							name: "directInvestProjectRunCashFlowHome",
							query: {
								id: this.id,
								unit: this.$route.query.unit,
								name: this.$route.query.name,
							}
						})
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//取消按钮
			addCancel(){
				this.$router.push({
					name: "directInvestProjectRunCashFlowHome",
					query: {
						id: this.id,
						unit: this.$route.query.unit,
						name: this.$route.query.name,
					}
				})
			},
			//---------------- 添加功能模块结束
			


			// ----------------查看功能模块开始
			getInitData(){
				getDistributeDetail({
					id:this.$route.query.jkId,
					c_type:this.$route.query.c_type,
				}).then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						let data = res.data.payload[0];
						this.datalistDetail = data.sub_funds;
						this.datalistDetail.forEach((element,index) => {
							element.index = index+1;
							element.name = element.fund_name;
							element.paied = element.real_amount;
							element.paied_rate = element.real_rate*100;
						});

						this.datalistTotal = res.data.payload;
						// this.datalistTotal[0].distribute_rate = this.datalistTotal[0].distribute_rate*100;
						delete this.datalistTotal[0].sub_funds;


					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			
			editTotal(index,row){
				// console.log(row);
				this.currentIndex = index;
				this.editorTotalData = JSON.parse(JSON.stringify(row)) ;
			},
			editCancelTotal(index,row){
				
				this.currentIndex = -1;
				this.datalistTotal[index] = JSON.parse(JSON.stringify(this.editorTotalData));
				// this.getInitData()
			},
			editSaveTotal(index,row){
				this.currentIndex = -1;
			},
			
			editDetail(index,row){
				// console.log(row);
				this.currentIndexDetail = index;
				this.editorDetailData = JSON.parse(JSON.stringify(row)) ;
			},
			editCancelDetail(index,row){
				this.currentIndexDetail = -1;
				this.datalistDetail[index] = JSON.parse(JSON.stringify(this.editorDetailData));
				this.calculate()
			},
			editSaveDetail(index,row){
				this.currentIndexDetail = -1;
				// console.log(row);
				// editSaveDetailCompanyRunCashIncomeManageApi({
				// 	id: row.id,
				// 	count: row.count,
				// 	fund_id: row.fund_id,
				// 	fund_type: row.fund_type,
				// 	should_pay: row.should_pay,
				// 	pay: row.pay,
				// 	unit: row.unit,
				// 	date: row.date,
				// }).then(res => {
				// 	console.log(res);
				// 	if(res.data.msgNo == 1) {
				// 		dialogConfirm("success", res.data.message);
				// 		this.reload()
				// 	} else {
				// 		dialogConfirm("info", res.data.message);
				// 	}
				// })
			},
			//---------------- 查看功能模块结束
			
			//---------------- 计算所有数据的总和
			// toCheck(index){
			// 	if(parseFloat(this.datalistDetail[index].paied)< parseFloat(this.datalistDetail[index].capital_distribute)){
			// 		dialogConfirm("info", '分配本金应小于或等于实缴出资!');
			// 		this.datalistDetail[index].capital_distribute = 0;
			// 		// return false;
			// 		this.calculate();
			// 	}
			// },

			calculate(index){
				if(index!=undefined){
					if(parseFloat(this.datalistDetail[index].paied)< parseFloat(this.datalistDetail[index].capital_distribute)){
						dialogConfirm("info", '分配本金应小于或等于实缴出资!');
						this.datalistDetail[index].capital_distribute = 0;
					}
				}
				let DetailData = this.datalistDetail;//明细数据
				let totalData = this.datalistTotal[0];//汇总数据
				let distribute_capital = 0;
				let distribute_profit = 0;
				let distribute_amount = 0;
				this.datalistDetail.forEach((element,index) => {
					if(element.capital_distribute==''){
						element.capital_distribute = 0;
					}
					if(element.profit_distribute_a==''){
						element.profit_distribute_a = 0;
					}
					if(element.distribute_amount==''){
						element.distribute_amount = 0;
					}
					if(element.paied===0||element.paied==='0'||element.paied==='0.0'||element.paied==='0.00'){
						element.paied = 0;
					}

					element.distribute_amount = parseFloat(element.capital_distribute) + parseFloat(element.profit_distribute_a) ;//分配总额
					element.all_distribute_amount =  parseFloat(element.distribute_amount) + parseFloat(element.accumulation_distribute) ;//累计分配总额
					if(element.paied){
						element.all_distribute_rate = ( (parseFloat(element.capital_distribute) +  parseFloat(element.profit_distribute_b))/ parseFloat(element.paied) )*100;//累计分配率
						element.all_distribute_rate = element.all_distribute_rate.toFixed(3);
					}else{
						element.all_distribute_rate = 0;
					}
					//汇总数据变化
					distribute_capital = distribute_capital + parseFloat(element.capital_distribute);//分配本金
					distribute_profit = distribute_profit + parseFloat(element.profit_distribute_b) ;//分配收益税前
					distribute_amount = distribute_amount + parseFloat(element.distribute_amount) ;//分配总额
				});
				this.datalistTotal[0].distribute_capital = distribute_capital;
				this.datalistTotal[0].distribute_profit = distribute_profit;
				this.datalistTotal[0].distribute_amount = distribute_amount;
			},
			
			chooseDate(date){
				if (!date) {
					this.datalistTotal[0].event_name = '';
					this.datalistTotal[0].event_count = '';
					return
				}
				this.getEventCountData()
			},
			getEventCountData(){
				var date = this.datalistTotal[0].plan_receive_date;
				if (this.is_add_page) {
					var event_id = '';
				} else{
					var event_id = this.datalistTotal[0].id;
				}
				getEventByDateApi({
					type: 5,
					id: this.id,
					cash_type: 1,
					date: date,
					fund_type: '',
					fund_id: '',
					event_id: event_id
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.datalistTotal[0].event_name = res.data.payload.event_name;
						this.datalistTotal[0]['event_count'] = res.data.payload.event_count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
